import React, { Component } from "react";
import Article from "../elements/Article";
import getTheme from '../../themeUtil'
const theme = getTheme()

class Articles extends Component {
  constructor(props) {
    super();
    this.state = { articles: [] };
  }

  componentDidMount() {
    const devTo = "https://api.rss2json.com/v1/api.json?rss_url=https://medium.com/feed/@khot.harish1207";

    fetch(devTo)
      .then(response => {
        return response.json();
      })
      .then(({items: data}) => {
        // console.log(data)
        let articles = [];
        data = data.slice(0, 4);
        data.forEach((element, index) => {
          articles.push(
            <div className="column" key={index}>
              <Article
                key={index}
                title={element.title}
                url={element.link}
                image={element.thumbnail}
                extract={element.content}
              />
            </div>
          );
        });
        var offset = 4 - data.length;
        for (var i = 0; i < offset; i++) {
          articles.push(<div key={i+ "_offset"} className="column"></div>);
        }
        this.setState({ articles: articles });
      });
  }

  render() {
    return (
      <section className="section" id="articles">
        <div className="container">
          <h1 className={`title ${theme.text2}`}>Articles</h1>
          <h2 className="subtitle is-4">My latest articles</h2>
          <div className="columns">{this.state.articles}</div>
        </div>
      </section>
    );
  }
}

export default Articles;
